.main_nav a {
  transition: all 0.4s linear;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-size: 0.93vmax;
  line-height: 1.66vmax;
  height: 3.22vmax;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-content: center;
  background-color: #2f373b;
  align-items: center;
  background: #333b3f; /* Old browsers */
  background: -moz-linear-gradient(top, #333b3f 0%, #2e363a 98%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #333b3f 0%, #2e363a 98%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #333b3f 0%, #2e363a 98%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  padding-right: 1.3vmax;
}
.main_nav a:hover {
  background: #31bdbb; /* Old browsers */
  background: -moz-linear-gradient(top, #31bdbb 0%, #18a3a1 79%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #31bdbb 0%, #18a3a1 79%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #31bdbb 0%, #18a3a1 79%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.main_nav .active {
  background: #31bdbb; /* Old browsers */
  background: -moz-linear-gradient(top, #31bdbb 0%, #18a3a1 79%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #31bdbb 0%, #18a3a1 79%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #31bdbb 0%, #18a3a1 79%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
