  overflow: hidden;
}
.work_box a {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: inherit;
  background-color: rgba(45,184,185,0.7);
  opacity: 0;
  color: #fff;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: all 0.4s linear;
}
.work_box a:hover {
